<template>
  <div>
    <keep-alive>
      <router-view v-if='$route.meta.keepAlive'></router-view>
    </keep-alive>
    <router-view v-if='!$route.meta.keepAlive'></router-view>

  </div>
</template>

<script>


export default {
  name: 'alert-index',
  methods: {
    showAlertList () {
      this.$router.push({
        name: 'alertList'
      })
    },
    showAlertDetail (id) {
      this.$router.push({
        name: 'alertDetail',
        params: {
          id: id
        }
      })
    }
  },
  mounted () {
    this.$bus.$on('showAlertList', this.showAlertList);
    this.$bus.$on('showAlertDetail', this.showAlertDetail);
  },
  beforeDestroy () {
    this.$bus.$off('showAlertList');
    this.$bus.$off('showAlertDetail');
  }
}
</script>

<style scoped>
.card {
  width: 90%;
  margin-left: 5%;
  border-radius: 15px;
  padding-top: 20px;
  min-height: 800px;
}
.table {
  width: 94%;
  margin-left: 3%;
  font-size: 20px;
}
.link {
  cursor: pointer;
}
.floatButton {
  position: fixed;
  right: 100px;
  font-size: 25px;
  cursor: pointer;
}
.floatButtonRefresh {
  bottom: 160px;
}
.floatButtonMark {
  bottom: 80px;
}
.empty {
  margin-top: 280px;
}
</style>